<template>
<div class="outer">
  <div class="type-nav">
    <div class="container">
      <div @mouseleave="leaveShow">
        <h2 class="all" @mouseenter="changeShow"></h2>
        <!-- ||-->
        <transition name="sort">
          <div class="sort" v-show="show">
            <!--  -->
            <div class="all-sort-list2" @click="goSearch">
              <div
                class="item"
                v-for="(c1, index) in categoryList"
                :key="c1.categoryId"
                @mouseenter="changeIndex(index)"
              >
                <h3 :class="{show: currentIndex === index }">
                  <a
                    :data-categoryName="c1.categoryName"
                    :data-category1Id="c1.categoryId"
                    >{{ c1.categoryName }}</a
                  >
                </h3>
                <div
                  class="item-list clearfix"
                  :style="{
                    display: currentIndex === index ? 'block' : 'none',
                  }"
                >
                  <div
                    class="subitem"
                    v-for="(c2, index) in c1.categoryChild"
                    :key="c2.categoryId"
                  >
                    <dl class="fore">
                      <dt>
                        <a
                          :data-categoryName="c2.categoryName"
                          :data-category2Id="c2.categoryId"
                          >{{ c2.categoryName }}</a
                        >
                      </dt>
                      <dd>
                        <em
                          v-for="(c3, index) in c2.categoryChild"
                          :key="c3.categoryId"
                        >
                          <a
                            :data-categoryName="c3.categoryName"
                            :data-category3Id="c3.categoryId"
                            >{{ c3.categoryName }}</a
                          >
                        </em>
                      </dd>
                    </dl>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </transition>
      </div>
      <nav class="nav">
        <a></a>
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
      </nav>
    </div>
  </div>
</div>
</template>
<script>
import {mapState} from 'vuex'
//lodash
//
import {throttle} from 'lodash'
export default {
    name: 'TypeNav',
    data(){
   return{
     //
     currentIndex:-1,
     show:true
     
    }
    },
    methods:{

    changeIndex:throttle(function(index){
      //currentIndex
   //
    //
      this.currentIndex=index;
    },50),
  goSearch(event){

  //
      //a div h3 dd dt em a
      //event
      let nodeElement = event.target;
      //adata-categoryNamedata-categoryNamea
      //datasetKV
      //categorynamea
      let { categoryname, category1id, category2id, category3id } =
        nodeElement.dataset;
      //ifa
      if (categoryname) {
        //
        let location = { name: "search" };
        //  let location = { name: "detail" };//
        let query = { categoryName: categoryname };
        //a
        if (category1id) {
          query.category1Id = category1id;
          //a
        } else if (category2id) {
          query.category2Id = category2id;
          //a
        } else {
          query.category3Id = category3id;
        }
        //paramsparamssearch
        if (this.$route.params) {
          //query
          location.query = query;
          //params
          location.params = this.$route.params;
          //paramssearch
          //home->search
          //search->search
          if (this.$route.path != "/home") {
            this.$router.replace(location);
          } else {
            this.$router.push(location);
          }
        }
      } 
  },
    changeShow() {
      //
      //serach
      if (this.$route.path != "/home") {
        this.show = true;
      }
    },
    //show
    leaveShow() {
      //
      this.currentIndex = -1;
      //
      if (this.$route.path != "/home") {
        this.show = false;
      }
    },
  },
  
    mounted(){
      this.$store.dispatch('categoryList');
     //showfalse
    //  this.show=false
    //Homeshowfalse
    if(this.$route.path!='/home'){
      this.show=false
    }
    },
    computed: {
      ...mapState({
        //
        //state
       categoryList: (state) => state.home.categoryList,
      })
    },
};
</script>

<style scoped>
/*.item:hover{
  background-color: rgb(115, 132, 122);
}*/
.item-list a{
  text-decoration: none;
}
.cur:hover{
  background-color: rgb(22, 202, 130);
}
.outer .type-nav .container {
  width: 1200px;
  margin: 0 auto;
  display: flex;
  position: relative;
}
.outer .type-nav .container .all {
  width: 210px;
  height: 45px;
  background-color: #36a97b;
  line-height: 35px;
  text-align: center;
  color: rgb(30, 29, 29);
  font-size: 14px;
  font-weight: 2000;
}
.outer .type-nav .container .nav a {
  height: 45px;
  margin: 0 22px;
  line-height: 45px;
  font-size: 16px;
  color: #333;
}
.outer .type-nav .container .sort {
  left: 0px;
  top: 45px;
  width: 210px;
  height: 461px;
  position: absolute;
  background: #fafafa;
  z-index: 999;
}
.outer .type-nav .container .sort .all-sort-list2 .item h3 {
  line-height: 30px;
  font-size: 14px;
  font-weight: bold;
  overflow: hidden;
  padding: 0 20px;
  margin: 0;
}
.outer .type-nav .container .sort .all-sort-list2 .item h3 a {
  color: rgb(203, 21, 21);
  text-decoration: none;
}
.outer .type-nav .container .sort .all-sort-list2 .item .item-list {
  display: none;
  position: absolute;
  width: 734px;
  min-height: 460px;
  background: #779663;
  left: 210px;
  border: 1px solid #ddd;
  top: 0;
  z-index: 9999 !important;
}
.outer .type-nav .container .sort .all-sort-list2 .item .item-list .subitem {
  float: left;
  width: 650px;
  padding: 0 4px 0 8px;
}
.outer .type-nav .container .sort .all-sort-list2 .item .item-list .subitem dl {
  border-top: 1px solid #eee;
  padding: 6px 0;
  overflow: hidden;

}
.outer .type-nav .container .sort .all-sort-list2 .item .item-list .subitem dl.fore {
  border-top: 0;
}
.outer .type-nav .container .sort .all-sort-list2 .item .item-list .subitem dl dt {
  float: left;
  width: 60px;
  line-height: 22px;
  text-align: right;
  padding: 3px 6px 0 0;
  font-weight: 700;
}
.outer .type-nav .container .sort .all-sort-list2 .item .item-list .subitem dl dd {
  float: left;
  width: 400px;
  padding: 3px 0 0;
  overflow: hidden;
}
.outer .type-nav .container .sort .all-sort-list2 .item .item-list .subitem dl dd em {
  float: left;
  height: 14px;
  line-height: 14px;
  padding: 0 8px;
  margin-top: 5px;
  border-left: 1px solid #ccc;
}

/*.outer .type-nav .container .sort .all-sort-list2 .item:hover .item-list{
  display:block;
}*/
</style>